import { Util } from '@/config/util';

export const staffDataGet = {
	//获取部门列表
	getDepartList: function(data, callback) {
		Util.DataEncSubmit('department_get.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	},
	//修改部门信息
	modifyDepartInfo:function(data, callback) {
		Util.DataEncSubmit('department_save.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	},
	//获取员工列表
	getStaffList:function(data, callback) {
		Util.DataEncSubmit('employee_get.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	},
	//获取单个部门的员工列表
	getDepartStaffList:function(data, callback) {
		Util.DataEncSubmit('department_get.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	},
	//修改员工的信息（点击操作）(冻结，启用，删除)
	modifyStaffInfo:function(data, callback) {
		Util.DataEncSubmit('user_info.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	},
	//点击获取验证码
	getPhoneTest:function(data, callback) {
		Util.DataEncSubmit('customer_save.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	},
	//验证手机验证码成功获取用户信息
	//邀请员工并保存信息
	getUserInfor:function(data,callback) {
		Util.DataEncSubmit('employee_save.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	},
	//职位编辑
	editDutyRight:function(data,callback) {
		Util.DataEncSubmit('position_save.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	},
	//职级列表获取
	getDutyList:function(data,callback) {
		Util.DataEncSubmit('position_get.php', data, (resp) => {
			if(callback && typeof callback === "function") {
				callback(resp);
			}
		});
	}

}


