<template>
	<div id="editDutyInfo">
		<div class="page_title">
			<router-link :to="{path:'/shop/staff',query:{name:choosePoMa}}">
				<span>职级管理</span>
			</router-link>
			<span>></span>
			<span>添加职位</span>
		</div>
		<div class="page_card">
			<div class="from_card">
				<el-form ref="form" :model="form" label-width="100px" size="mini" :rules="rules">
					<el-form-item label="职位名称" prop="position_name">
						<span v-if="!form.entry_type||form.entry_type=='1'">{{form.position_name}}</span>
						<el-input v-model="form.position_name" v-else></el-input>

					</el-form-item>
					<el-form-item label="权限" prop="right">
						<el-radio-group v-model="form.right" fill="#4877E7">
							<el-radio-button label="点餐收银"></el-radio-button>
							<el-radio-button label="后台管理"></el-radio-button>
						</el-radio-group>
					</el-form-item>

					<el-form-item label="">
						<el-checkbox-group v-model="form.type">
							<p>
								<el-checkbox label="启用" name="type"></el-checkbox>
							</p>
							<p>
								<el-checkbox label="全选" name="type"></el-checkbox>
							</p>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item label="">
						<p>模块名称</p>
						<el-checkbox-group v-model="form.arrRight">
							<el-checkbox label="后台全部权限" name="arrRight"></el-checkbox>
							<el-checkbox label="使用预定" name="arrRight"></el-checkbox>
							<el-checkbox label="使用历史订单管理" name="arrRight"></el-checkbox>
							<el-checkbox label="结账" name="arrRight"></el-checkbox>
							<el-checkbox label="使用退款申请" name="arrRight"></el-checkbox>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item label="">
						<div class="dash"></div>
						<p class="title">用户请求管理</p>
						<p class="newOrder">新订单管理</p>
						<el-checkbox-group v-model="form.newOrder">
							<el-checkbox label="使用新订单管理" name="newOrder"></el-checkbox>
							<el-checkbox label="下单并结账" name="newOrder"></el-checkbox>
							<el-checkbox label="关闭并退款" name="newOrder"></el-checkbox>
						</el-checkbox-group>
						<p class="refondTitle">退款申请管理</p>
						<el-checkbox-group v-model="form.refund">
							<el-checkbox label="使用退款申请" name="refund"></el-checkbox>
						</el-checkbox-group>
					</el-form-item>
				</el-form>
				<div class="line" v-if="form.right=='点餐收银'"></div>
			</div>
			<div class="btnGroup">
				<el-button class="save" @click="saveStaffInfo('form')">保存</el-button>
				<el-button class="saveAdd" @click="saveAddStaffInfor('form')" v-if="$route.query.infor == undefined">保存并继续添加</el-button>
				<el-button class="cancel" @click="cancelStaffInfor('form')">取消</el-button>
			</div>
		</div>
	</div>
</template>

<script>
	import { staffDataGet } from "../../api";
	import { treeMethods } from "../../index";
	export default {
		data() {
			return {
				choosePoMa: false,
				form: {
					position_name: "",
					entry_type: "",
					right: "点餐收银",
					type: [],
					arrRight: [],
					newOrder: [],
					refund: []
				},
				rules: {
					position_name: [{
						required: true,
						message: '请选择职位',
						trigger: 'click'
					}],
					right: [{
						required: true,
						message: '请选择部门',
						trigger: 'change'
					}]
				}
			}
		},
		created() {
			this.initData()
		},
		methods: {
			//初始化数据
			initData() {
				if(this.$route.query.infor !== undefined) {
					this.form.position_name = this.$route.query.infor.position_name;
					this.form.entry_type = this.$route.query.infor.entry_type
					this.form.arrRight = this.$route.query.infor.arrRight;
				}
			},
			//返回职级管理页面
			backToPoMa() {
				this.$emit("choosePoMa", this.choosePoMa)
				this.$router.go(-1);
			},
			//点击保存
			saveStaffInfo(formName) {
				this.$refs[formName].validate(valid => {
					if(valid) {
						this.getRightList();
					}
				})
			},
			//获取权限
			getRightList() {
				if(this.$route.query.infor !== undefined) {
					let data = {
						save_position: 1,
						position_id: this.$route.query.infor.position_id, //新建时不传
						position_name: this.$route.query.infor.position_name,
						position_note: this.$route.query.infor.position_note,
						position_permission: this.$route.query.infor.arrId
					}
				} else {
					var a = treeMethods.judgeStaffList(this.form.arrRight, 1)
					let data = {
						save_position: 1,
						position_name: this.form.position_name,
						position_note: this.form.position_note,
						position_permission: a
					}
				}

				staffDataGet.editDutyRight(data, (resp) => {
					console.log(resp)
				})
			},
			//点击保存并继续添加
			saveAddStaffInfor(formName) {
				this.$refs[formName].validate(valid => {
					if(valid) {
						this.$refs[formName].resetFields();
					}
				})
			},
			//点击取消
			cancelStaffInfor(formName) {
				this.$refs[formName].validate(valid => {
					if(valid) {
						this.$refs[formName].resetFields();
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.page_card {
		width: 100%;
		min-height: 580px;
		padding-bottom: 60px;
		background: #ffffff;
		box-shadow: 0 2px 4px 0 #becaeb;
	}
	
	.page_title {
		@include wh(100%, 40px);
		@include fc(12px, #333333);
		margin-bottom: 14px;
		line-height: 40px;
	}
	
	.from_card {
		width: 45%;
		padding: 48px 0 40px 0;
		margin-left: 133px;
	}
	
	.dash:after {
		content: "";
		width: 100%;
		position: absolute;
		top: 1%;
		left: 0;
		border-bottom: 1px dashed lightgray;
	}
	
	.line:after {
		content: "";
		width: 84.2%;
		position: absolute;
		top: 90%;
		left: 230px;
		border-bottom: 1px solid lightgray;
	}
	
	p {
		height: 25px;
	}
	
	.title {
		margin-top: 20px;
	}
	
	.refondTitle {
		margin-top: 30px;
	}
	
	.btnGroup {
		margin-top: 50px;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#editDutyInfo {
		.el-input {
			width: 320px;
		}
		.el-input__inner {
			border-radius: 0;
			height: 34px;
		}
		.el-input__inner:hover {
			border: 1px solid #bfcbd9;
		}
		.el-input__inner:focus {
			border: 1px solid #bfcbd9;
		}
		.btnGroup {
			.el-button {
				min-width: 100px;
				height: 34px;
				@include fc(14px, #ffffff);
				border: none;
				margin-left: 20px;
				background: #4877E7;
			}
			.save {
				margin-left: 500px;
			}
			.cancel {
				background: #ffffff;
				color: #4877E7;
				border: 1px solid #4877E7;
			}
		}
	}
</style>


